import React from 'react';
import './style.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faLinkedinIn } from '@fortawesome/free-brands-svg-icons';
import { faHeart, faEnvelope } from '@fortawesome/free-solid-svg-icons';

const SocialMedia = () => {
    return ( 
        <div className='social-media-wrap'>
            <div className='social-media'><a href='https://github.com/irensula'><FontAwesomeIcon icon={faGithub} className='social-media__icon'></FontAwesomeIcon></a></div>
            <div className='social-media'><a href='https://www.linkedin.com/in/irina-sula-022296129/'><FontAwesomeIcon icon={faLinkedinIn} className='social-media__icon'></FontAwesomeIcon></a></div>
            <div className='social-media'><a href='mailto:irensula19@gmail.com'><FontAwesomeIcon icon={faEnvelope} className='social-media__icon'></FontAwesomeIcon></a></div>
            {/* <div className='social-media' id='star-icon'><FontAwesomeIcon icon={faStar} className='social-media__icon'></FontAwesomeIcon></div> */}
            <div className='social-media' id='heart-icon'><FontAwesomeIcon icon={faHeart} className='social-media__icon'></FontAwesomeIcon></div>
        </div>
     );
}
 
export default SocialMedia;