import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHtml5, faCss3, faBootstrap, faReact, faSquareJs } from '@fortawesome/free-brands-svg-icons';
import {ReactComponent as Jquery} from './../img/icons/jquery.svg';

import Header from './../components/header/Header';
import SocialMedia from './../components/socialmedia/SocialMedia';

const Home = () => {
    return ( 
        <>
        <div className='main-container'>
            <SocialMedia />
            <Header />
        </div>
        

        <main className="section">
            <div className="container">
            <h1 className="title-1">Skills</h1>
                    <ul className="skills-list">
                        <li className="content-list__item">
                            <FontAwesomeIcon className='skill-icon' icon={faHtml5}></FontAwesomeIcon>
                            <h2 className="title-2">HTML 5</h2>
                        </li>
                        <li className="content-list__item">
                            <FontAwesomeIcon className='skill-icon' icon={faCss3}></FontAwesomeIcon>
                            <h2 className="title-2">CSS 3</h2>
                        </li>
                        <li className="content-list__item">
                            <FontAwesomeIcon className='skill-icon' icon={faBootstrap}></FontAwesomeIcon>
                            <h2 className="title-2">Bootstrap</h2>
                        </li>
                        <li className="content-list__item">
                            <FontAwesomeIcon className='skill-icon' icon={faReact}></FontAwesomeIcon>
                            <h2 className="title-2">React</h2>
                        </li>
                        <li className="content-list__item">
                            <FontAwesomeIcon className='skill-icon' icon={faSquareJs}></FontAwesomeIcon>
                            <h2 className="title-2">JavaScript</h2>   
                        </li>
                        <li className="content-list__item">
                            <Jquery className='icon-jquery' width='150' height='150' />
                            <h2 className="title-2">jQuery</h2>
                        </li>
                    </ul>
            </div>
        </main>
        </>
     );
}
 
export default Home;